import { Metadata } from 'next';
import { getTranslations } from 'next-intl/server';
import ScientificCalculatorClient from './ScientificCalculatorClient';

export async function generateMetadata({ params }: { params: Promise<{ locale: string }> }): Promise<Metadata> {
    const { locale } = await params;
    const t = await getTranslations({ locale, namespace: 'Tools.scientific-calculator.meta' });

    return {
        title: t('title'),
        description: t('description'),
    };
}

export default async function ScientificCalculatorPage({ params }: { params: Promise<{ locale: string }> }) {
    const { locale } = await params;
    return <ScientificCalculatorClient />;
}
